import React from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
} from "@vkontakte/vkui";

const ModalBuyTransfer = ({
  setActiveModal,
  onChangeHandlerAmount,
  type,
  onChangeQiwiAccount,
  withdraw,
  APP_AVATAR,
}) => {
  return (
    <>
      {type === "withdraw" ? (
        <ModalRoot activeModal="withdraw">
          <ModalCard
            id="withdraw"
            onClose={() => setActiveModal(null)}
            icon={
              <Avatar
                size={72}
                src={APP_AVATAR}
              />
            }
            header="Вывод средств"
            caption={
              <>
                <FormLayout>
                  <Input
                    top="Введите номер QIWI Кошелька"
                    placeholder="+7xxxxxxxxxx"
                    onChange={onChangeQiwiAccount}
                    type="text"
                  />
                  <Input
                    top="Введите сумму в рублях"
                    placeholder="10"
                    onChange={onChangeHandlerAmount}
                    type="number"
                  />
                  <InfoRow id="comission">Комиссия РефралОЧКИ: 10%</InfoRow>
                </FormLayout>
              </>
            }
            actions={[
              {
                title: "Вывести",
                mode: "commerce",
                action: () => {
                  withdraw();
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : (
        ""
      )}
    </>
  );
};

export { ModalBuyTransfer };
