import React from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import Icon16LikeOutline from "@vkontakte/icons/dist/16/like_outline";
import Icon16Like from "@vkontakte/icons/dist/16/like";
import Icon12Verified from "@vkontakte/icons/dist/12/verified";

import { formatBalance } from "../helpers";

const TopUsersItem = ({
  id,
  firstName,
  lastName,
  photoURL,
  amount,
  idVk,
  referralsCount,
  accountType,
  nameColor,
  profit,
}) => {
  const urlUser = "https://vk.com/id" + idVk;

  return (
    <Div className="itemShop">
      <span style={{ paddingLeft: "15px", color: "#11729c" }}>{id}</span>

      <Cell
        style={{ width: "100%" }}
        before={
          photoURL ? (
            <Link href={urlUser} target="_blank">
              <Avatar src={photoURL} />
            </Link>
          ) : (
            <Avatar src={"https://vk.com/images/camera_200.png"} />
          )
        }
        description={
          <Div className="itemMainTopUsersDescription">
            <Div className="itemMainTopUsersLike">
              <Div style={{ whiteSpace: "nowrap" }}>
                Прибыль: {formatBalance(profit)} ₽
              </Div>
            </Div>
            <Div className="itemMainTopUsersLike">
              <Div style={{ whiteSpace: "nowrap" }}>
                Рефералов: {referralsCount}
              </Div>
            </Div>
          </Div>
        }
      >
        <Div
          style={{
            display: "flex",
            justifyContent: "start",
            alignItems: "center",
            flexDirection: "row",
            marginLeft: "10px",
          }}
        >
          <>
            <span
              style={{
                color: [, "green", "#D40000", "blue", "#800080"][nameColor],
              }}
            >
              {firstName + " " + lastName}
            </span>
            {accountType > 2 ? (
              <Icon12Verified
                // onClick={openMenuVIP}
                style={{ marginLeft: "5px" }}
                className="animationIcons"
                fill={[, "green", "#D40000", "blue", "#800080"][nameColor]}
                width={16}
                height={16}
              />
            ) : (
              <></>
            )}
          </>
        </Div>
      </Cell>
    </Div>
  );
};

export { TopUsersItem };
