import React, { useState, useEffect, useReducer } from "react";
import {
  Panel,
  Div,
  Button,
  Gallery
} from "@vkontakte/vkui";

import { dataSlider } from "../helpers";
import { SliderItem } from "../items";

const HowItWorks = ({ go, id, setActivePanel }) => {
  const [slideIndex, setSlideIndex] = useState(1);


  const renderSliderItem = dataSlider.map((item, index) => {
    
    return (
      <SliderItem
        key={index}
        id={item.id}
        headerLeft={item.headerLeft}
        headerRight={item.headerRight}
        step={item.step}
        mainTop={item.mainTop}
        mainBottom={item.mainBottom}
        slideIndex={slideIndex}
        setSlideIndex={setSlideIndex}
        setActivePanel={setActivePanel}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <Div
        style={{
          display: "flex",
          flexDirection: "row",
          justifyContent: "center",
          alignItems: "center",
          padding: "15px",
          height: "80%",
        }}
      >
        {/* <Gallery
          // timeout="5000"
          slideIndex={slideIndex}
          onChange={(slideIndex) => setSlideIndex(slideIndex)}
          slideWidth="50%"
          align="center"
          style={{
            height: "400px",
            marginTop: "10px",
          }}
        >
         {renderSliderItem}
        </Gallery> */}
        {renderSliderItem}
        
      </Div>
    </Panel>
  );
};

export { HowItWorks };
