import React from "react";
import { Div, Button } from "@vkontakte/vkui";
import Icon12Fire from "@vkontakte/icons/dist/12/fire";
import Icon16LikeOutline from "@vkontakte/icons/dist/16/like_outline";
import Icon16Like from "@vkontakte/icons/dist/16/like";
import Icon12Verified from "@vkontakte/icons/dist/12/verified";

const SliderItem = ({
  headerLeft,
  headerRight,
  step,
  mainTop,
  mainBottom,
  setSlideIndex,
  slideIndex,
  setActivePanel,
}) => {
  return (
    <Div
      style={{
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        alignItems: "center",
        background: "#03023d",
        color: "white",
        padding: "20px",
        height: "90%",
        // width: "50%",
        borderRadius: "12px",
      }}
    >
      <Div
        style={{
          margin: "0px 0px 5px 0px",
          fontWeight: "bold",
          fontSize: "20px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "start",
          width: "100%",
        }}
      >
        {headerLeft}
      </Div>

      <Div
        style={{
          margin: "0px 0px 10px 0px",
          fontWeight: "bold",
          fontSize: "20px",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          alignItems: "flex-end",
          width: "100%",
        }}
      >
        {headerRight}
      </Div>

      <Div>
        <Div
          style={{
            margin: "10px 0px 5px 0px",
            // fontWeight: "bold",
            fontSize: "20px",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            width: "100%",
          }}
        >
          <Div>Шаг {step}</Div>
          <Div>{mainTop}</Div>
        </Div>
        {/* <Div
          style={{
            margin: "0px 0px 10px 0px",
            // fontSize: "20px",
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            width: "100%",
          }}
        >
          Поделись ссылкой
        </Div> */}
        <Div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "start",
            margin: "10px 0px 10px 0px",
            width: "100%",
          }}
        >
          {mainBottom}
        </Div>
      </Div>

      <Div
        style={{
          margin: "40px 0px 10px 0px",
          width: "100%",
        }}
      >
        <Button
          style={{
            backgroundColor: "green",
            color: "white",
          }}
          mode="commerce"
          size="l"
          stretched
          onClick={() => {
            if (slideIndex < 3) {
              setSlideIndex(slideIndex + 1);
            } else {
              setActivePanel("home");
            }
          }}
        >
          Далее
        </Button>
      </Div>
    </Div>
  );
};

export { SliderItem };
