import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  Avatar,
  InfoRow,
  Div,
} from "@vkontakte/vkui";

const ModalStart = ({setActiveModal, APP_AVATAR, openMenuVIP}) => {
  return (
    <ModalRoot activeModal="modal-start">
      <ModalCard
        id="modal-start"
        onClose={() => setActiveModal(null)}
        icon={<Avatar size={72} src={APP_AVATAR} />}
        header="Как работает РефералОЧКА?"
        caption={
          <>
            <Div
              style={{
                display: "flex",
                alignItems: "start",
                justifyContent: "center",
                flexDirection: "column",
              }}
            >
              <Div>1. Активируешь аккаунт (чем выше уровень, тем больше % прибыли)</Div>
              <Div>2. Делишься своей реферальной ссылкой в истории, на стене, с друзьями.</Div>
              <Div>3. Друзья переходят по твоей ссылке и активируют аккаунт.</Div>
              <Div>4. Ты получаешь прибыль, выводишь на QIWI Кошелёк</Div>
            </Div>
          </>
        }
        actions={[
          {
            title: "Активировать",
            mode: "commerce",
            action: () => {
              setActiveModal(null);
              openMenuVIP();
            },
          },
        ]}
      ></ModalCard>
    </ModalRoot>
  );
};

export { ModalStart };
