import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  InfoRow,
  FormLayoutGroup,
  Div,
  Radio,
} from "@vkontakte/vkui";
import Icon12Verified from "@vkontakte/icons/dist/12/verified";

const ModalInfoVIP = ({ setActiveModal, openPaymentVIP, type, APP_AVATAR }) => {
  const [choose, setChoose] = useState(2);

  const targetId = (e) => {
    setChoose(+e.currentTarget.id);
    console.log("targetId choose", choose);
    console.log("targetId ", +e.currentTarget.id);
  };

  return (
    <>
      {type === "Бронзовый" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={APP_AVATAR} />}
            header="Что дает Бронзовый аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль 20% с каждого пополнения рефералов.
                  </InfoRow>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 9,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Бронзовый");
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "Серебряный" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={APP_AVATAR} />}
            header="Что дает Серебряный аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль 30% с каждого пополнения рефералов.
                  </InfoRow>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 19,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Серебряный");
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "Золотой" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={APP_AVATAR} />}
            header="Что дает Золотой аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль 40% с каждого пополнения рефералов.
                  </InfoRow>
                  <InfoRow>2. Цветной никнейм в ТОП-е.</InfoRow>

                  <InfoRow style={{ marginBottom: "10px" }}>
                    Выберите цвет:{" "}
                  </InfoRow>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                      flexDirection: "row",
                      width: "100%",
                    }}
                  >
                    <Div
                      id="1"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "green",
                        border: choose === 1 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="2"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#D40000",
                        border: choose === 2 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="3"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "blue",
                        border: choose === 3 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="4"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#800080",
                        border: choose === 4 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                  </Div>

                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 39,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Золотой", choose);
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : type === "Платиновый" ? (
        <ModalRoot activeModal="modal-info-vip">
          <ModalCard
            id="modal-info-vip"
            onClose={() => setActiveModal(null)}
            icon={<Avatar size={72} src={APP_AVATAR} />}
            header="Что дает Платиновый аккаунт"
            caption={
              <>
                <Div
                  style={{
                    display: "flex",
                    alignItems: "start",
                    justifyContent: "center",
                    flexDirection: "column",
                  }}
                >
                  <InfoRow>
                    1. Прибыль 60% с каждого пополнения рефералов.
                  </InfoRow>

                  <InfoRow>2. Цветной никнейм в ТОП-е.</InfoRow>
                  <InfoRow>
                    <Div
                      style={{
                        display: "flex",
                        flexDirection: "row",
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Div> 3. Значок верификации в ТОП-е.</Div>
                      <Icon12Verified
                        style={{ marginLeft: "5px" }}
                        className="animationIcons"
                        fill={[,"green", "#D40000", "blue", "#800080"][choose]}
                        width={18}
                        height={18}
                      />
                    </Div>
                  </InfoRow>
                  <InfoRow style={{ marginBottom: "10px" }}>
                    Выберите цвет:{" "}
                  </InfoRow>
                  <Div
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-around",
                      flexDirection: "row",
                      width: "100%",
                    }}
                  >
                    <Div
                      id="1"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "green",
                        border: choose === 1 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="2"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#D40000",
                        border: choose === 2 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="3"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "blue",
                        border: choose === 3 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                    <Div
                      id="4"
                      onClick={targetId}
                      style={{
                        borderRadius: "20px",
                        height: "30px",
                        width: "30px",
                        backgroundColor: "#800080",
                        border: choose === 4 ? "3px solid #FFB800" : "",
                      }}
                    ></Div>
                  </Div>
                  <InfoRow style={{ marginTop: "10px" }}>
                    К оплате: 69,00 руб.
                  </InfoRow>
                </Div>
              </>
            }
            actions={[
              {
                title: "Активировать",
                mode: "commerce",
                action: () => {
                  openPaymentVIP("Платиновый", choose);
                },
              },
            ]}
          ></ModalCard>
        </ModalRoot>
      ) : (
        ""
      )}
    </>
  );
};

export { ModalInfoVIP };
