import React, { useState, useEffect, useReducer } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  Panel,
  PanelHeader,
  Button,
  Div,
  ActionSheet,
  ActionSheetItem,
  Slider,
  Select,
  FixedLayout,
  PromoBanner,
} from "@vkontakte/vkui";
import Icon16Dropdown from "@vkontakte/icons/dist/16/dropdown";
import Icon28RefreshOutline from "@vkontakte/icons/dist/28/refresh_outline";

import { formatDate, APIManager, formatBalance } from "../helpers";

const { callMethod } = APIManager;

const Home = ({
  id,
  go,
  setPopout,
  setActivePanel,
  openPopoutSharing,
  openAlert,
  snackbar,
  openInfoVIP,
  openTransfer,
  setActiveModal,
  popout,
  openMenuVIP,
  activated,
  balance,
  accountType: at,
  updateUserInfo,
  GROUP_ID,
  openInfoStart,
  getPlatform,
}) => {
  const [referrals, setReferrals] = useState(0);
  const [profit, setProfit] = useState([234]);
  const [referralsLocal, setReferralsLocal] = useState(130);
  const [accountType, setAccountType] = useState("Бронзовый");
  const [promoBannerProps, setPromoBannerProps] = useState();
  const [activeBanner, setActiveBanner] = useState(null);

  const renderPromoBannerProps = {
    title: "Заголовок",
    domain: "vk.com",
    trackingLink: "https://vk.com",
    ctaText: "Перейти",
    advertisingLabel: "Реклама",
    iconLink:
      "https://sun9-7.userapi.com/c846420/v846420985/1526c3/ISX7VF8NjZk.jpg",
    description: "Описание рекламы",
    ageRestrictions: 14,
    statistics: [
      { url: "", type: "playbackStarted" },
      { url: "", type: "click" },
    ],
  };

  useEffect(() => {
    console.log("вызвали useEffect");
    bridge
      .send("VKWebAppGetAds")
      .then((promoBannerProps) => {
        console.log("promoBannerProps 1", promoBannerProps);
        console.log("promoBannerProps 2", { promoBannerProps });
        setPromoBannerProps({
          title: promoBannerProps.title,
          domain: promoBannerProps.domain,
          trackingLink: promoBannerProps.trackingLink,
          ctaText: promoBannerProps.ctaText,
          advertisingLabel: promoBannerProps.advertisingLabel,
          iconLink: promoBannerProps.iconLink,
          description: promoBannerProps.description,
          ageRestrictions: promoBannerProps.ageRestrictions,
          statistics: promoBannerProps.statistics,
        });
      })
      .catch((err) => console.log("promoBannerProps ERR", err));
  }, []);

  useEffect(() => {
    callMethod("getUserReferralsCount").then(({ message }) => {
      setReferrals(message);
    });
    setTimeout(() => {
      addGroup();
    }, 7000);

    // setTimeout(() => {
    //   openInfoStart();
    // }, 20000);
  }, []);

  function handleChangeSlider(referralsLocal) {
    switch (accountType) {
      case "Бронзовый":
        setProfit((referralsLocal * 9 * 0.2).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      case "Серебряный":
        setProfit((referralsLocal * 9 * 0.3).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      case "Золотой":
        setProfit((referralsLocal * 9 * 0.4).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      case "Платиновый":
        setProfit((referralsLocal * 9 * 0.6).toFixed(2));
        setReferralsLocal(referralsLocal);
        break;
      default:
        break;
    }
  }

  function addGroup() {
    bridge
      .send("VKWebAppJoinGroup", { group_id: GROUP_ID })
      .then(({ result }) => {
        console.log("VKWebAppJoinGroup", result);
      });
  }

  function AddToCommunity() {
    bridge.send("VKWebAppAddToCommunity", {});
  }

  function openActions() {
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem
            autoclose
            onClick={() => openPopoutSharing("stories")}
          >
            Позвать друзей
          </ActionSheetItem>
          {/* <ActionSheetItem autoclose onClick={openGeneralRules}>
            Правила
          </ActionSheetItem> */}
          <ActionSheetItem autoclose onClick={AddToCommunity}>
            Добавить в группу
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function openSelectAccount() {
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Бронзовый");
              setProfit((referralsLocal * 9 * 0.2).toFixed(2));
            }}
          >
            Бронзовый
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Серебряный");
              setProfit((referralsLocal * 9 * 0.3).toFixed(2));
            }}
          >
            Серебряный
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Золотой");
              setProfit((referralsLocal * 9 * 0.4).toFixed(2));
            }}
          >
            Золотой
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => {
              setAccountType("Платиновый");
              setProfit((referralsLocal * 9 * 0.6).toFixed(2));
            }}
          >
            Платиновый
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  return (
    <Panel id={id} separator={false}>
      <PanelHeader
        left={
          <Button
            style={{
              marginLeft: "20px",
              background: "inherit",
              // borderColor: "#03023d",
              // borderWidth: "2px",
              padding: "0px",
            }}
          >
            <Icon28RefreshOutline
              width={24}
              height={24}
              fill="#11729c"
              // style={{ marginLeft: "20px" }}
              onClick={updateUserInfo}
            />
          </Button>
        }
      >
        РефералОЧКА
      </PanelHeader>

      <>
        <Div
          style={{
            padding: "15px",
            height: getPlatform === "web" ? "800px" : "auto",
          }}
        >
          <Div className="panelHomeAccount">
            {activated ? (
              <>
                <Div className="flexSpaceBetweenRow">
                  <Div>Аккаунт:</Div>
                  <Div>
                    {[, "Бронзовый", "Серебряный", "Золотой", "Платиновый"][at]}
                  </Div>
                </Div>
                <Div className="flexSpaceBetweenRow">
                  <Div>
                    <Button
                      style={{
                        backgroundColor: "green",
                        color: "white",
                      }}
                      mode="commerce"
                      size="m"
                      stretched
                      onClick={openInfoStart}
                    >
                      Правила
                    </Button>
                  </Div>
                  <Div>
                    <Button
                      style={{
                        backgroundColor: "green",
                        color: "white",
                      }}
                      mode="commerce"
                      size="m"
                      stretched
                      onClick={openMenuVIP}
                    >
                      Изменить
                    </Button>
                  </Div>
                </Div>
              </>
            ) : (
              <>
                <Div className="flexSpaceBetweenRow">
                  <Div>Аккаунт:</Div>
                  <Div>Не активирован</Div>
                </Div>
                <Div className="flexSpaceBetweenRow">
                  <Div></Div>
                  <Div>
                    <Button
                      style={{
                        backgroundColor: "green",
                        color: "white",
                      }}
                      mode="commerce"
                      size="m"
                      stretched
                      onClick={openMenuVIP}
                    >
                      Активировать
                    </Button>
                  </Div>
                </Div>
              </>
            )}
          </Div>

          <Div className="panelHomeBalance">
            <Div className="flexSpaceBetweenRow">
              <Div>Баланс:</Div>
              <Div>{formatBalance(balance)} ₽</Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div>
                <Button
                  style={{
                    backgroundColor: "#03023d",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  stretched
                  onClick={go}
                  data-to="operations-history"
                  // onClick={() => setActivePanel("how-it-works")}
                >
                  История
                </Button>
              </Div>
              <Div>
                <Button
                  style={{
                    backgroundColor: "#03023d",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  stretched
                  onClick={() => openTransfer("withdraw")}
                >
                  Вывести
                </Button>
              </Div>
            </Div>
          </Div>

          <Div className="panelHomeReferrals">
            <Div className="flexSpaceBetweenRow">
              <Div>Рефералов:</Div>
              <Div>{referrals}</Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div></Div>
              <Div>
                <Button
                  style={{
                    backgroundColor: "#11729c",
                    color: "white",
                  }}
                  mode="commerce"
                  size="m"
                  stretched
                  onClick={openPopoutSharing}
                >
                  Пригласить
                </Button>
              </Div>
            </Div>
          </Div>

          <Div className="panelHomeProfitCalculation">
            <Div className="flexSpaceBetweenRow">
              <Div>Ваш аккаунт:</Div>

              <Div>
                <Button
                  style={{
                    backgroundColor: "#7a078c",
                    color: "white",
                  }}
                  after={<Icon16Dropdown fill="white" />}
                  mode="commerce"
                  size="m"
                  stretched
                  onClick={openSelectAccount}
                >
                  {accountType}
                </Button>
              </Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div>Расчет прибыли:</Div>
              <Div>{profit} ₽</Div>
            </Div>
            <Div className="flexSpaceBetweenRow">
              <Div>Рефералов:</Div>
              <Div>{referralsLocal} </Div>
            </Div>

            <Slider
              min={1}
              max={500}
              step={1}
              onChange={(referralsLocal) => handleChangeSlider(referralsLocal)}
              defaultValue={referralsLocal}
              // top="Минимальная прибыль"
              // bottom={`${profit}`}
            />
          </Div>

          {promoBannerProps && (
            <FixedLayout vertical="bottom">
              <PromoBanner
                bannerData={promoBannerProps}
                onClose={() => setPromoBannerProps(null)}
              />
            </FixedLayout>
          )}

          {/* <FixedLayout vertical="bottom">
              <PromoBanner
                bannerData={promoBannerProps}
                // onClose={() => setPromoBannerProps(null)}
              />
            </FixedLayout> */}
        </Div>
      </>
      {snackbar}
    </Panel>
  );
};

export { Home };
