import React from "react";
import { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Div,
} from "@vkontakte/vkui";

import { OperationsHistoryItem } from "../items";
import { APIManager, formatBalance } from "../helpers";

const { callMethod } = APIManager;

const OperationsHistory = ({ id, go, fetchedUser, GROUP_ID, APP_AVATAR }) => {
  const [dataOperationsHistory, setDataOperationsHistory] = useState([]);

  useEffect(() => {
    getOperationsHistory();
  }, []);

  function getOperationsHistory() {
    callMethod("getOperationsList").then(({ status, message }) => {
      setDataOperationsHistory(message);
      console.log("getOperationsHistory", message);
    });
  }

  function formatDate(date) {
    date = new Date(date * 1000);

    let dd = date.getDate();
    if (dd < 10) dd = "0" + dd;
    let mm = date.getMonth() + 1;
    if (mm < 10) mm = "0" + mm;
    let yy = date.getFullYear() % 100;
    if (yy < 10) yy = "0" + yy;
    let hh = date.getHours();
    if (hh < 10) hh = "0" + hh;
    let minutes = date.getMinutes();
    if (minutes < 10) minutes = "0" + minutes;

    return dd + "." + mm + "." + yy + " " + hh + ":" + minutes;
  }

  const renderOperationsHistoryItem = dataOperationsHistory.map(
    (item, index) => {
      return (
        <OperationsHistoryItem
          key={index}
          type={item.type}
          amount={formatBalance(item.amount)}
          userId={item.userId}
          createdAt={item.createdAt}
          profitUserId={item.profitUserId}
          profitUserPhotoURL={item.profitUserPhotoURL}
          profitUserName={item.profitUserName}
          formatDate={formatDate}
          fetchedUser={fetchedUser}
          GROUP_ID={GROUP_ID}
          APP_AVATAR={APP_AVATAR}
          accountType={item.accountType}
        />
      );
    }
  );

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple left={<PanelHeaderBack onClick={go} data-to="home" />}>
        История операций
      </PanelHeaderSimple>

      {!dataOperationsHistory.length ? (
        <Div
          style={{
            marginLeft: 30,
            marginTop: 30,
            display: "flex",
            justifyContent: "center",
            flexDirection: "row",
            alignItems: "center",
          }}
        >
          У вас еще нету ни одной операции.
        </Div>
      ) : (
        <Div>{renderOperationsHistoryItem}</Div>
      )}
    </Panel>
  );
};

export { OperationsHistory };
