import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  View,
  ScreenSpinner,
  TabbarItem,
  Epic,
  Div,
  Tabbar,
  ConfigProvider,
  ActionSheet,
  ActionSheetItem,
  Snackbar,
  Avatar,
  CellButton,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";

import Icon16Done from "@vkontakte/icons/dist/16/done";
import Icon16Cancel from "@vkontakte/icons/dist/16/cancel";
import Icon28UserCircleOutline from "@vkontakte/icons/dist/28/user_circle_outline";
import Icon28Favorite from "@vkontakte/icons/dist/28/favorite";

import "./styles/reset.sass";
import "./styles/icons.sass";
import "./styles/panels.sass";
import "./styles/items.sass";
import "./styles/components.sass";

import { Logger, APIManager } from "./helpers";

import {
  APP_ID,
  GROUP_ID,
  QIWI_PUBLIC_KEY,
  APP_AVATAR,
  APP_IMG_SHARING,
  APP_IMG_SHARING_WALL,
} from "./config";
import { Home, HowItWorks, OperationsHistory, Top, AdminPanel } from "./panels";
import { ModalInfoVIP, ModalBuyTransfer, ModalStart } from "./modal";

const vk = new Logger("vk");

const attempts = new Map();
const USER_ID = Number(
  new URLSearchParams(document.location.search).get("vk_user_id")
);

const { callMethod } = APIManager;

const App = () => {
  let amount = 0;
  let targetId = 0;
  let qiwiAccount = "";
  // let getPlatform = "";

  const [popout, setPopout] = useState();
  const [referrals, setReferrals] = useState("0");
  const [snackbar, setSnackbar] = useState(null);
  const [fetchedUser, setUser] = useState(null);
  const [dataUsers, setDataUsers] = useState([]);
  const [activeModal, setActiveModal] = useState(null);
  const [activePanel, setActivePanel] = useState("home");
  const [groupsTop, setGroupsTop] = useState([]);
  const [globalTop, setGlobalTop] = useState([]);
  const [friendsTop, setFriendsTop] = useState([]);
  const [isBanned, setIsBanned] = useState(false);
  const [getPlatform, setGetPlatform] = useState(false);

  // Новые переменные

  // Активирован аккаунт или нет
  const [activated, setActivated] = useState(false);
  // Баланс игрока в рублях
  const [balance, setBalance] = useState(0);
  // Тип аккаунта: 1 - Бронзовый, 2 - Серебряный, 3 - Золотой, 4 - Платиновый
  const [accountType, setAccountType] = useState(0);

  useEffect(() => {
    bridge.subscribe(({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        const schemeAttribute = document.createAttribute("scheme");
        schemeAttribute.value = data.scheme ? data.scheme : "client_light";
        document.body.attributes.setNamedItem(schemeAttribute);
      }
    });
  }, []);

  useEffect(() => {
    // Определение ОС
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        setGetPlatform(result.platform);
        // getPlatform = result.platform;

        console.log("result.platform", getPlatform);
      })
      .catch((err) => console.log(err));
  }, [getPlatform]);

  useEffect(() => {
    setTimeout(nativeAds, 1000);
  }, []);

  useEffect(() => {
    window.addEventListener("popstate", (e) => e.preventDefault() & menu(e));
  }, []);

  function menu(e) {
    setActivePanel("home");

    // setActivePanel(e.currentTarget.dataset.to);
    // setActiveTab("home");
  }

  function nativeAds() {
    if (![236908027, 457421325, 73606509].includes(USER_ID)) {
      return bridge
        .sendPromise("VKWebAppShowNativeAds", {
          ad_format: "preloader",
        })
        .then((res) => console.log("VKWebAppShowNativeAds RES", res))
        .catch((err) => console.log("VKWebAppShowNativeAds ERR", err));
    }
  }

  function updateUserInfo(from = "") {
    let referrerId = null;

    if (from === "useEffect") {
      referrerId = document.location.hash;
    }

    callMethod("init", { referrerId }).then(({ message: user }) => {
      setBalance(user.balance);
      setActivated(user.activated);
      setAccountType(user.accountType);
    });
  }

  useEffect(() => {
    updateUserInfo("useEffect");

    console.log("accountType", accountType);

    // Работа с ВК
    bridge.send("VKWebAppGetUserInfo").then(setUser);
  }, []);

  function qiwiPayVIP(type, choose) {
    window.QiwiCheckout.createInvoice({
      publicKey: QIWI_PUBLIC_KEY,
      amount: {
        Бронзовый: 9,
        Серебряный: 19,
        Золотой: 39,
        Платиновый: 69,
      }[type],
      comment: {
        Бронзовый: `bronze 0 ${USER_ID} (не менять)`,
        Серебряный: `silver 0 ${USER_ID} (не менять)`,
        Золотой: `gold ${choose} ${USER_ID} (не менять)`,
        Платиновый: `platinum ${choose} ${USER_ID} (не менять)`,
      }[type],
    })
      .then((response) => {
        console.log("qiwi vip", response);
      })
      .catch((error) => {
        console.log("QIWI VIP ERROR:", error);
      });
  }

  function payVkPayVIP(type, choose) {
    bridge
      .sendPromise("VKWebAppOpenPayForm", {
        app_id: APP_ID,
        action: "pay-to-group",
        params: {
          amount: {
            Бронзовый: 9,
            Серебряный: 19,
            Золотой: 39,
            Платиновый: 69,
          }[type],
          group_id: GROUP_ID,
          description: {
            Бронзовый: `bronze 0 (не менять)`,
            Серебряный: `silver 0 (не менять)`,
            Золотой: `gold ${choose} (не менять)`,
            Платиновый: `platinum ${choose} (не менять)`,
          }[type],
        },
      })
      .then((response) => {
        console.log("vkpay vip", response);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  function withdraw() {
    if (!amount || Number.isNaN(+amount)) {
      setActiveModal(null);
      return openAlert("Сумма должна быть числом", "red");
    }

    if (amount > balance) {
      setActiveModal(null);
      return openAlert("Не хватает средств для вывода", "red");
    }

    if (amount < 10) {
      setActiveModal(null);
      return openAlert("Минимальная сумма вывода 10 руб.", "red");
    }

    if (!qiwiAccount.startsWith("+7")) {
      setActiveModal(null);
      return openAlert("Номер кошелька должен начинаться с +7", "red");
    }

    setActiveModal(null);

    callMethod("withdrawal", {
      amount,
      qiwiAccount,
    }).then(({ status, message }) => {
      openAlert(message, status === "denied" ? "red" : "green");
      updateUserInfo();
    });
  }

  function openPaymentVIP(type, choose) {
    setActiveModal(null);
    bridge
      .sendPromise("VKWebAppAllowMessagesFromGroup", {
        group_id: GROUP_ID,
      })
      .then((res) => {
        console.log("result", res);
        if (res) {
          setPopout(
            <>
              <ActionSheet onClose={() => setPopout(null)}>
                <ActionSheetItem
                  autoclose
                  onClick={() => qiwiPayVIP(type, choose)}
                >
                  Оплатить через QIWI
                </ActionSheetItem>
                <ActionSheetItem
                  autoclose
                  onClick={() => payVkPayVIP(type, choose)}
                >
                  Оплатить через VK Pay
                </ActionSheetItem>
              </ActionSheet>
            </>
          );
        }
      });
  }

  function onChangeQiwiAccount(e) {
    const value = e.target.value.trim();
    qiwiAccount = value;
  }

  function onChangeHandlerId(e) {
    const value = e.target.value.trim();
    targetId = value;

    console.log("onChangeHandlerId targetId", targetId);
  }

  function onChangeHandlerAmount(e) {
    const value = e.target.value.trim();
    amount = +value;

    if (value < 0) {
      amount = +value;
    }

    const element = document.querySelector("#comission");

    if (element) {
      element.innerHTML = `Комиссия PornCoin ${(amount * 0.1).toFixed(
        2
      )} руб.</br>К оплате: ${(amount * 1.1).toFixed(2)} руб.`;
    }
  }

  function openInfoVIP(type) {
    setActiveModal(
      <ModalInfoVIP
        setActiveModal={setActiveModal}
        openPaymentVIP={openPaymentVIP}
        type={type}
        APP_AVATAR={APP_AVATAR}
      />
    );
  }

  function openInfoStart() {
    setActiveModal(
      <ModalStart
        setActiveModal={setActiveModal}
        APP_AVATAR={APP_AVATAR}
        openMenuVIP={openMenuVIP}
      />
    );
  }

  function openTransfer(type) {
    setActiveModal(
      <ModalBuyTransfer
        setActiveModal={setActiveModal}
        onChangeHandlerAmount={onChangeHandlerAmount}
        type={type}
        onChangeHandlerId={onChangeHandlerId}
        onChangeQiwiAccount={onChangeQiwiAccount}
        withdraw={withdraw}
        APP_AVATAR={APP_AVATAR}
      />
    );
  }

  function shareLink() {
    const link = `https://vk.com/app${APP_ID}#${USER_ID}`;
    bridge.send("VKWebAppShare", {
      link: link,
    });
  }

  // Копирование в буфер
  function copyLink() {
    const link = `https://vk.com/app${APP_ID}#${USER_ID}`;
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        console.log(result.platform);
        if (result.platform === "web" || result.platform === "mobile-web") {
          window.navigator.clipboard.writeText(link).then(
            () => {
              console.log("successfully set", link);
              openAlert("Ссылка скопирована");
            },
            () => {
              console.log("write failed", link);
            }
          );
        } else {
          bridge.send("VKWebAppCopyText", { text: link });
          openAlert("Ссылка скопирована");
        }
      })
      .catch((error) => {
        console.log("error", error);
      });
  }

  //  Поделиться в истории
  function story() {
    const url = `https://vk.com/app${APP_ID}#${USER_ID}`;

    bridge.sendPromise("VKWebAppShowStoryBox", {
      background_type: "image",
      url: APP_IMG_SHARING,
      attachment: {
        text: "go_to",
        type: "url",
        url: url,
      },
    });
  }

  //  ДОБАВЛЕНИЕ РЕПОСТА НА СТЕНУ ПОЛЬЗОВАТЕЛЯ
  function share(e) {
    e.preventDefault();
    const url = `https://vk.com/app${APP_ID}#${USER_ID}`;
    const urlPhotoStories = `${APP_IMG_SHARING_WALL},https://vk.com/app${APP_ID}#${USER_ID}`;
    const textStories = `Приглашай друзей, выводи прибыль!, Приложение - ${url}`;

    bridge.send("VKWebAppShowWallPostBox", {
      message: textStories,
      attachments: urlPhotoStories,
    });
  }

  function openPopoutSharing(type) {
    setActiveModal(null);
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={shareLink}>
            Поделиться ссылкой
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={copyLink}>
            Скопировать ссылку
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={() => story(type)}>
            Поделиться в истории
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={(e) => share(e, type)}>
            Поделиться на стене
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function openMenuVIP() {
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={() => openInfoVIP("Бронзовый")}>
            Бронзовый
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={() => openInfoVIP("Серебряный")}>
            Серебряный
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={() => openInfoVIP("Золотой")}>
            Золотой
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={() => openInfoVIP("Платиновый")}>
            Платиновый
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function getHash() {
    const { hash } = window.location;

    if (!hash || Number.isNaN(+hash.replace("#", ""))) {
      return;
    }

    const referrerId = Number(hash.replace("#", ""));
  }

  function openAlert(text, backgroundColor = "green") {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        duration="7000"
        layout="horizontal"
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ backgroundColor }}>
            {backgroundColor === "green" ? (
              <Icon16Done fill="#fff" width={14} height={14} />
            ) : (
              <Icon16Cancel fill="#fff" width={14} height={14} />
            )}
          </Avatar>
        }
      >
        {text}
      </Snackbar>
    );
  }

  const stub = (textDiv, retryFunction, textButton) => {
    return (
      <Div className="divSpinner">
        <Div>{textDiv}</Div>
        <Div>
          <CellButton onClick={retryFunction}>{textButton}</CellButton>
        </Div>
      </Div>
    );
  };

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

  return (
    <>
      {isBanned ? (
        <Div className="flexCenterRow">
          Вы забанены, по поводу разблокировки обратитесь к администрации
          PornCoin
        </Div>
      ) : (
        <>
          <Epic
            tabbar={
              <>
                {activePanel === "how-it-works" ? (
                  <></>
                ) : (
                  <Tabbar>
                    <TabbarItem onClick={go} data-to="top" text="Топы">
                      {activePanel === "top" ? (
                        <Icon28Favorite fill="#11729c" />
                      ) : (
                        <Icon28Favorite />
                      )}
                    </TabbarItem>
                    <TabbarItem onClick={go} data-to="home" text="Главная">
                      {activePanel === "home" ? (
                        <Icon28UserCircleOutline fill="#11729c" />
                      ) : (
                        <Icon28UserCircleOutline />
                      )}
                    </TabbarItem>
                  </Tabbar>
                )}
              </>
            }
          >
            <ConfigProvider isWebView={true}>
              <View
                activePanel={activePanel}
                popout={popout}
                modal={activeModal}
                header={false}
              >
                <Home
                  id="home"
                  fetchedUser={fetchedUser}
                  go={go}
                  referrals={referrals}
                  setReferrals={setReferrals}
                  getPlatform={getPlatform}
                  setPopout={setPopout}
                  popout={popout}
                  setActivePanel={setActivePanel}
                  openPopoutSharing={openPopoutSharing}
                  snackbar={snackbar}
                  openAlert={openAlert}
                  openInfoVIP={openInfoVIP}
                  openTransfer={openTransfer}
                  setActiveModal={setActiveModal}
                  openMenuVIP={openMenuVIP}
                  activated={activated}
                  balance={balance}
                  accountType={accountType}
                  updateUserInfo={updateUserInfo}
                  GROUP_ID={GROUP_ID}
                  openInfoStart={openInfoStart}
                />
                <HowItWorks
                  id="how-it-works"
                  go={go}
                  openPopoutSharing={openPopoutSharing}
                  setActivePanel={setActivePanel}
                />
                <OperationsHistory
                  id="operations-history"
                  go={go}
                  fetchedUser={fetchedUser}
                  GROUP_ID={GROUP_ID}
                  APP_AVATAR={APP_AVATAR}
                />
                <Top
                  id="top"
                  dataUsers={dataUsers}
                  go={go}
                  groupsTop={groupsTop}
                  globalTop={globalTop}
                  friendsTop={friendsTop}
                  // vk={vk}
                  snackbar={snackbar}
                  setGroupsTop={setGroupsTop}
                  setGlobalTop={setGlobalTop}
                  openAlert={openAlert}
                  stub={stub}
                  openMenuVIP={openMenuVIP}
                  activated={activated}
                />
                <AdminPanel
                  id="admin-panel"
                  go={go}
                  openAlert={openAlert}
                  snackbar={snackbar}
                />
              </View>
            </ConfigProvider>
          </Epic>
        </>
      )}
    </>
  );
};

export default App;
