import React, { useState, useEffect } from "react";
import { Cell, Div, Avatar, Link } from "@vkontakte/vkui";
import Icon16Down from "@vkontakte/icons/dist/16/down";
import Icon16Up from "@vkontakte/icons/dist/16/up";
import Icon16Done from "@vkontakte/icons/dist/16/done";

const OperationsHistoryItem = ({
  type,
  amount,
  userId,
  createdAt,
  profitUserId,
  formatDate,
  fetchedUser,
  profitUserPhotoURL,
  profitUserName,
  GROUP_ID,
  APP_AVATAR,
  accountType,
}) => {
  // const photoURLTargetId = "https://vk.com/id" + createdAt;
  const groupId = "https://vk.com/id" + GROUP_ID;

  return (
    <>
      {type === "account" ? (
        <Cell
          // className="itemMain"
          before={
            APP_AVATAR ? (
              <Link
                href={groupId}
                target="_blank"
                style={{ userSelect: "none" }}
              >
                <Avatar src={APP_AVATAR} />
              </Link>
            ) : (
              <Avatar src={"https://vk.com/images/camera_200.png"} />
            )
          }
          description={
            <Div style={{ marginLeft: 20 }}>
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  flexDirection: "row",
                  justifyContent: "start",
                }}
              >
                <Div style={{ color: "red" }}>- {amount} ₽</Div>
              </Div>
            </Div>
          }
          asideContent={
            <Div
              style={{
                marginTop: "-16px",
                display: "flex",
                justifyContent: "end",
                flexDirection: "row",
                alignItems: "end",
                fontSize: "10px",
              }}
            >
              {formatDate(createdAt)}
            </Div>
          }
        >
          <Div
            style={{
              marginBottom: "5px",
              paddingLeft: "15px",
              display: "flex",
              justifyContent: "start",
              flexDirection: "row",
              alignItems: "center",
              fontSize: "16px",
            }}
          >
            <Div>
              <Icon16Done fill="green" />
            </Div>
            <Div style={{ marginTop: 4, marginLeft: 4, userSelect: "none" }}>
              Покупка аккаунта
              <span style={{ fontWeight: "bold", marginLeft: "3px" }}>
                «
                {
                  [, "Бронзовый", "Серебряный", "Золотой", "Платиновый"][
                    accountType
                  ]
                }
                »
              </span>
            </Div>
          </Div>
        </Cell>
      ) : type === "withdrawal" ? (
        <Cell
          // className="itemMain"
          before={
            APP_AVATAR ? (
              <Link
                href={groupId}
                target="_blank"
                style={{ userSelect: "none" }}
              >
                <Avatar src={APP_AVATAR} />
              </Link>
            ) : (
              <Avatar src={"https://vk.com/images/camera_200.png"} />
            )
          }
          description={
            <Div style={{ marginLeft: 20 }}>
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  flexDirection: "row",
                  justifyContent: "start",
                }}
              >
                <Div style={{ color: "red" }}>-{amount} ₽</Div>
              </Div>
            </Div>
          }
          asideContent={
            <Div
              style={{
                marginTop: "-16px",
                display: "flex",
                justifyContent: "end",
                flexDirection: "row",
                alignItems: "end",
                fontSize: "10px",
              }}
            >
              {formatDate(createdAt)}
            </Div>
          }
        >
          <Div
            style={{
              marginBottom: "5px",
              paddingLeft: "15px",
              display: "flex",
              justifyContent: "start",
              flexDirection: "row",
              alignItems: "center",
              fontSize: "16px",
            }}
          >
            <Div>
              <Icon16Up fill="red" />
            </Div>
            <Div style={{ marginTop: 4, marginLeft: 4, userSelect: "none" }}>
              Вывод
            </Div>
          </Div>
        </Cell>
      ) : (
        <Cell
          before={
            profitUserPhotoURL ? (
              <Link
                href={`https://vk.com/id${profitUserId}`}
                target="_blank"
                style={{ userSelect: "none" }}
              >
                <Avatar src={profitUserPhotoURL} />
              </Link>
            ) : (
              <Avatar src={"https://vk.com/images/camera_200.png"} />
            )
          }
          description={
            <Div style={{ marginLeft: 20 }}>
              <Div>
                <Div
                  style={{
                    wordWrap: "break-word",
                    marginBottom: 5,
                    userSelect: "none",
                  }}
                >
                  {profitUserName}
                </Div>
              </Div>

              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  flexDirection: "row",
                  justifyContent: "start",
                }}
              >
                <Div style={{ color: "green" }}>+ {amount} ₽</Div>
              </Div>
            </Div>
          }
          asideContent={
            <Div
              style={{
                marginTop: "-16px",
                display: "flex",
                justifyContent: "end",
                flexDirection: "row",
                alignItems: "end",
                fontSize: "10px",
              }}
            >
              {formatDate(createdAt)}
            </Div>
          }
        >
          <Div
            style={{
              marginBottom: "5px",
              paddingLeft: "15px",
              display: "flex",
              justifyContent: "start",
              flexDirection: "row",
              alignItems: "center",
              fontSize: "16px",
            }}
          >
            <Div>
              <Icon16Down fill="green" />
            </Div>
            <Div style={{ marginTop: 4, marginLeft: 4, userSelect: "none" }}>
              Процент от реферала
            </Div>
          </Div>
        </Cell>
      )}
    </>
  );
};

export { OperationsHistoryItem };
